.pokemon-list {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 200px);
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .pokemon-item {
    margin: 15px;
    text-align: center;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 10px;
    transition: transform 0.2s;
  }
  
  .pokemon-item:hover {
    transform: scale(1.1);
    background-color: gray;
  }
  
  .pokemon-item img {
    width: 100px;
    height: 100px;
  }
  .logo {
    justify-content: center;
    display: grid;
    text-align: center;
  }
  .pokemonDetails {
    justify-content: center;
    display: grid;
    text-align: left;
    border: 1px solid black;
    padding: 10px;
  }
  .pokemonDetails img {
    width: 100%;
  }
  .pokemonDetails dialog {
    justify-content:right;
  }
 .loading  {
  justify-content: center;
 }
 .loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; 
}
.pokemonDetails button{
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: black;
}